  .clickEffect {
    position: absolute;
    font-family: 'Inder';
    color: rgb(161, 161, 161);
    text-align: left;
    z-index: 7;
    margin-top: -110px;
    margin-left: 20px;
    background: rgba(255, 255, 255, 0.534);
    padding-left: 5px;
    padding-right: 5px;
    display: none;
    border-radius: 10px;
  }
  
  .subject {
    margin-top: -52px;
  }
  
  .message {
    margin-top: -220px;
  }
  
  .formWrapper .submit {
    width: 100%;
    margin-left:0;
    color: white;
    text-transform: uppercase;
    background-color: black;
    border-color: transparent
  }
  
  #message {
    height: 200px;
  }

  .invalid {
    color: red;
    font-size: 13px !important;
    position: absolute;
    left: 45%;
  }

  .form-group {
    margin-bottom: 1.3em
  }

  .confirm{
    position: absolute;
    width: 100%;
    height: 100%;
    background-color: rgba(192,192,192,0.3);
    z-index: 20 !important;
    top: 0%;
    left: 0%;

  }

  .confirmWrap {
    position: relative;
    top:50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    background: -webkit-linear-gradient(top left,#FDABAB, #FDCFB3, #FFEBEB);
    background: linear-gradient(to bottom right,#FDABAB, #FDCFB3, #FFEBEB);
    width: 60%;
    height: 60%;
    border-radius: 20px;
    text-align: center;
    box-shadow: 5px 10px 28px #7c7c7c;
  }

  .confirmWrap h1{
    position: relative;
    color: white;
    font-weight: 900;
    font-size: 2.5em;
    top:50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
  }

  .confirmWrap button{
    position: relative;
    bottom:100%;
    margin-top: 300px;
    background: white;
    border: transparent;
    border-radius: 5px;
    padding: 10px 20px;
    box-shadow: 5px 5px 8px #7c7c7c;
    font-weight: 600;
    color:#5e5d5d
  }
@media screen and (min-width: 900px),screen and (min-height: 449px) {
  .menu {
    overflow: hidden;
    font-family: 'Oswald', sans-serif;
    z-index: 10;
    width: 100%;
    right: -10px;
    color: #f2f2f2;
  }

  .menu img {
    float: left;
    display: block;
    margin-left: 10px;
    margin-top: 30px;
    text-decoration: none;
    letter-spacing:2px;
    text-transform:uppercase;
    font-size:12px;
    font-weight: bold;
  }

  .btn{
    float: left;
    text-decoration: none;
    left: 30px;
    margin-left: 50px;
    margin-top: 30px;
    z-index: 10;
    background-color:rgba(0, 0, 0, 0.53);
    border-radius: 3px;
    padding:10px 30px;
    color: #f2f2f2;
  }

  .btn:hover {
    border:none;
    background-color:rgba(199, 198, 198, 0.73);
    padding:11px 35px;
    color:#1b1b1b;
    -webkit-transition-duration: .2s;
            transition-duration: .2s;
  }
  
  .menu .icon {
    display: none;
  }

  .menu img.navbar-brand {
    padding-top: 0px;
    width: auto;
    height: auto;
  }

  .menu .closebtn {
    display: none;
    cursor: pointer;
  }
}

@media screen and (min-width: 900px) {
  .sidenav {display: block !important;  z-index: 10 !important}
  
  .menu .icon {
    display: none !important;
  }
}

@media screen and (min-width:900px) {
  .menu {
    z-index: 20px;
  }
  .menu .icon {
    display: block;
  }
  .sidenav {
    width:auto !important;
    display: none;
    z-index: 10 !important
  }
  .btn{
    margin-left: 30px;
  }
}

@media screen and (max-width: 899px) {
  .menu {
    z-index: 20px;
    font-size: 15px
  }
  .menu img.navbar-brand {
    float: left;
  }

  .sidenav {
    height: 100%;
    width: 250px;
    position: fixed;
    top: 0;
    right: 0;
    background-color: #111;
    overflow-x: hidden;
    -webkit-transition: 0.5s;
    transition: 0.5s;
    padding-top: 60px;
    z-index: 10 !important;
    overflow: hidden;
  }

  .btn {
    float: left;
    margin-left: 20px;
    margin-right: 100px;
    padding: 8px 8px 8px 32px;
    text-decoration: none;
    font-size: 20px;
    color: #818181;
    display: block;
    -webkit-transition: 0.3s;
    transition: 0.3s;
  }

  .btn:hover:not(:first-child) {
    color: #f1f1f1;
    background:rgba(199, 198, 198, 0.73);
    padding:11px 35px;
    color:#1b1b1b;
    -webkit-transition-duration: .2s;
            transition-duration: .2s;
  }

  .bth:hover:first-child {
    color: #f1f1f1;
  }

  .menu .icon {
    color:#111;
    float: right;
    display: block;
    margin-top: 30px;
    margin-right: 50px;
    cursor:pointer;
    font-size: 30px;
  }

  .sidenav .closebtn {
    color: white;
    position: absolute;
    display: block;
    top: 0;
    right: 25px;
    font-size: 40px;
    margin-right: 5px;
  }
}

@media screen and (max-height: 450px) and (max-width: 900px) {
  .topnav {position: absolute; width: 100%; z-index: 10;}
  .btn {font-size: 15px; margin-right: 100px;}
  .sidenav .closebtn {font-size: 20px; margin-top: 10px;}
}
